<template>
  <div class="bigcontainer">
    <div class="event-card">
      <span>{{ product.name }} on {{ product.price }}</span>
      <h4>{{ product.image }}</h4>
      <button v-on:click="deleteProduct(product.id)">Delete Product</button>
    </div>
  </div>
</template>

<script>
import ProductService from '@/services/ProductService.js'

export default {
    name:"ProductCard",
    props:{
        product:{
            type: Object,
            required: true
        },
    },
    methods:{
        deleteProduct(id){
            console.log(id);
            ProductService.deleteProduct(id)
            .then((response) => {
                console.log(response);
            })
            .catch((error) => {
                console.log(error);
            });
        },
    },
};
</script>

<style></style>
